define('collection',["jquery", "underscore", "backbone", "svg", "model"], function($, _, Backbone, SVG, Model) {
    var Collection = Backbone.Collection.extend({
        initialize: function(){ },
    });

    var LineCollection = Collection.extend({
        model: Model.line
    });

    var RectCollection = Collection.extend({
        model: Model.rect
    });

    var DeviceCollection = Collection.extend({
        model: Model.device
    });

    return {
        base: Collection,
        line: LineCollection,
        rect: RectCollection,
        device: DeviceCollection
    }
});
